<template>
  <div class="animated fadeIn">
      <b-col md="12">
        <b-card>
          <div slot="header">
            Informasi <strong>JUKIR</strong>
          </div>
          <b-form>
            <b-form-group
              description="ID User"
              label="ID User"
              label-for="idUser"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="idUser" type="text" :disabled="true" :value="data.no"></b-form-input>
            </b-form-group>
            <b-form-group
              description="ID Area"
              label="ID Area"
              label-for="idArea"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="idArea" type="text" :disabled="true" :value="data.id_area"></b-form-input>
            </b-form-group>
            <b-form-group
              description="Juru Parkir ID"
              label="ID Jukir"
              label-for="idJukir"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="idJukir" type="text" :disabled="true" :value="data.id"></b-form-input>
            </b-form-group>

            <b-form-group
              description="Nama Juru Parkir"
              label="Nama"
              label-for="jukirName"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="jukirName" type="text" :value="data.nm">500</b-form-input>
            </b-form-group>

            <b-form-group
              description="Jam Operasional"
              label="Jam Operasional"
              label-for="jamOperasional"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="jamOperasional" type="text" :value="data.ktalm"></b-form-input>
            </b-form-group>
            <b-form-group
              description="Lokasi Kerja Juru Parkir"
              label="Lokasi"
              label-for="jukirLokasi"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="jukirLokasi" :textarea="true" :rows="3" :value="data.lks"></b-form-input>
            </b-form-group>

            <b-form-group
              description="Lokasi Jalan Juru Parkir"
              label="Jalan"
              label-for="jukirJalan"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="jukirJalan" type="text" :value="data.jalan"></b-form-input>
            </b-form-group>
            <b-form-group
              description="Titik Lokasi Juru Parkir"
              label="Titik"
              label-for="jukirTitik"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="jukirTitik" type="text" :value="data.ttk"></b-form-input>
            </b-form-group>

            <b-form-group
              label="Berlaku s.d."
              label-for="dateBerlaku"
              :label-cols="3"
              :horizontal="true">
              <b-form-input type="date" id="dateBerlaku" :value="data.sdt"></b-form-input>
            </b-form-group>

            <b-form-group
              description="No Telpon Jukir"
              label="Telephone"
              label-for="jukirPhone"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="jukirPhone" type="text"></b-form-input>
            </b-form-group>

            <b-form-group
              description="Photo Juru Parkir"
              label="Photo"
              label-for="jukirPhoto"
              :label-cols="3"
              :horizontal="true">
              <img class="thumbailPhoto" :src="'http://' + data.link_foto"></img>
            </b-form-group>

            <b-form-group
              label="File Photo"
              label-for="filePhoto"
              :label-cols="3"
              :horizontal="true">
              <b-form-file id="fileInput" :plain="true"></b-form-file>
            </b-form-group>


            <div slot="footer">
              <b-button variant="primary">Update</b-button>&nbsp;&nbsp;
              <b-button variant="danger">Reset</b-button>
            </div>
          </b-form>
        </b-card>
      </b-col>
  </div>
</template>

<script>
  import {EventBus} from '../../_auth/utils/eventbus'

  export default {
    name: 'detail-jukir',

    data() {
      return {
        data: [],
        errorMsg: ''
      }
    },

    beforeMount: function () {
      this.getData();
    },

    methods: {
      getData() {
        let str_id = this.$route.query.id;
        this.$store.dispatch('get_detail_jukir_by_exact_id', {id: str_id})
          .then(() => this.$router.push({
            path: 'detail-jukir',
            params: {id: str_id},
            query: {id: str_id}
          }))
      },
    },

    mounted() {
      EventBus.$on('success', (msg) => {
        this.data = msg[0];
        this.data.sdt = this.data.sdt.substr(0,10);
        this.errorMsg = "";
      });
      EventBus.$on('fail', (msg) => {
        this.data = {};
        this.errorMsg = msg;
      })
    },

    beforeDestroy() {
      EventBus.$off('success');
      EventBus.$off('fail');
    },
  };
</script>
